const createPersonalSETStarter = (reps = 20, breakAfterInSeconds = 30) => {
  let set = {
    reps,
    duration:undefined,
    repsNotes: undefined,
    breakAfterInSeconds,
    breakNotes: undefined
  }
  return set;
}
const createPersonalTRAININGStarter = (name) => {
  let training = {
    name,
    sets: [createPersonalSETStarter()]
  }
  return training;
}

const createPersonalPLANStarter = (name) => {
  let plan = {
    name,
    trainings: [createPersonalTRAININGStarter('mirecek')]
  }
  return plan;
}









export {  createPersonalPLANStarter, createPersonalTRAININGStarter as createPersonalTRAININGStarter, createPersonalSETStarter};