import React from 'react';
import { TestContext } from '../ShowPersonalPlanToUserToAdjust'

const Schedule = ({ children }) => {

  const context = React.useContext(TestContext);
  // console.log("schedule context:", context);
  let counter = 0;
  const clickIt = () => {
    context.setState1({ c: 'context changed', counter: context.state1.counter + 1 });
  }

  return (
    <div>
      <h1>Schedule</h1>

      <h2>Children from inside</h2>
      <div style={{ backgroundColor: 'red' }}>
        <button onClick={clickIt}>click me</button>
        <p>counter: {context.state1.counter}</p>
      </div>

      <h2>Children from outside</h2>
      <div style={{ backgroundColor: 'red' }}>{children}</div>
    </div>
  );
};

export default Schedule;