import React, { useContext, useEffect, useState } from 'react';
import { postJsonDataUrlAfterAPIpushups } from '../../functions/postJsonData';
import PushUpsContext from './state/PushUpsContextState';
// import PushUpsContextState from './state/PushUpsContextState'

import TrainingPlanOverview from './TrainingPlanOverview'
import PersonalPushupPlans from './PersonalPushupPlans';
// import { Link, Route, Switch, HashRouter } from 'react-router-dom';


const PushUpPlans = () => {
  let pushupContext = useContext(PushUpsContext);
  const [sstate, setsstate] = useState(null);
  // let ready=true;
  // let ready;
  useEffect(() => {
    // ready = false;
    // ready = true;
    console.log("ready?? ? ??");

    if (pushupContext.user && pushupContext.user.pushupPlans && pushupContext.user.pushupPlans.current) {
      // ready = true;
      setsstate(pushupContext.user.pushupPlans.current.name);
      console.log("ready!!!!!!!!!!!!!");
      // document.querySelector('#endCurrentPushupPlan').style.display = 'block';
      // document.querySelector('#usersCurrentPlanName').innerHTML = pushupContext.user.pushupPlans.current.name;

    } else {
      setsstate(null);

      // document.querySelector('#endCurrentPushupPlan').style.display = 'none';
    };
    // console.log("ready?", ready);
  }
    //activate on every re-render if empty
    , [pushupContext]   ///now runs on every rerender, with [] runs only on first
  )

  const commitToPlan = async (planID, planName) => {
    //assuming user exists...
    let updatedUser = await postJsonDataUrlAfterAPIpushups('/commit', { userID: pushupContext.user._id, planID, name: planName });
    pushupContext.updateUser(updatedUser);
  }

  const finishCurrentTrainingPlan = async () => {
    //assuming user and his current plan exists
    let updatedUser = await postJsonDataUrlAfterAPIpushups('/endPlan', {});
    pushupContext.updateUser(updatedUser);


  }

  return (
    <div>
      {/* <Link to='/pushups'>Return</Link> */}
      <h2>Training Plans</h2>
      <TrainingPlanOverview>
        <h3>3 days split - 5x Max reps</h3>
        <p>about the training</p>
        <br />
        <p>Commit to this training for 2 weeks.<button id='trainingPlanID_001' className='btnOpacity' onClick={() => commitToPlan(12, '3 days split - 5x Max reps')}> Commit</button></p>

      </TrainingPlanOverview>
      {sstate ?
        (<div>
          <p>You are currently commited to this plan: <span id='usersCurrentPlanName'>{sstate}</span></p>
          <br />
          <button onClick={finishCurrentTrainingPlan}>End your current training plan</button>
        </div>)
        : null}
      {/* <div id='endCurrentPushupPlan' style={{ display: 'none' }}>
        <p>You are currently commited to this plan: <span id='usersCurrentPlanName'></span></p>
        <br />
        <button onClick={finishCurrentTrainingPlan}>End your current training plan</button>
      </div> */}

      <PersonalPushupPlans/>
    </div>
  );
};

export default PushUpPlans;