import React from 'react';
import { SLink } from '../../../App';
import styled from 'styled-components';
import { createPersonalPLANStarter, createPersonalTRAININGStarter, createPersonalSETStarter, } from './cratingObjectsfunctions'
import ShowPersonalPlanToUserToAdjust from './ShowPersonalPlanToUserToAdjust';


const CreatingPersonalPushupPlan = () => {
  const [planX, setPlanX] = React.useState(null);

  const createPersonalPushupPlan = () => {
    let newPlan = createPersonalPLANStarter("My First Personal Plan");
    setPlanX(newPlan);
    // console.log("plan se to:", newPlan);
  }

  React.useEffect(() => {
    // console.log("hi from useEffect.");
    createPersonalPushupPlan();
  }, []);
  // console.log("plan:", planX);


  //Setting up CONTEXT
  const PersonalPushupPlanContext = React.createContext({
    plan: null,
    updateUplan: () => { }
  })
  // const PersonalPushupPlanContextPROVIDER = ({children})


  return (
    <div>
      <SLink to='/pushups/trainingPlans'>Plans</SLink>
      <h3>Create your new personal pushup plan</h3>
      <button onClick={createPersonalPushupPlan}>Start</button>
      {planX ? <ShowPersonalPlanToUserToAdjust planToAdjust={planX} /> : null}
    </div>
  );
};

export default CreatingPersonalPushupPlan;