import React from 'react';
import styled from 'styled-components';
import { SLink } from './Home';
import { Route, Switch, } from 'react-router-dom';



const SPersonalPushupPlans = styled.div`
background-color: yellow;
`

const PersonalPushupPlans = () => {




  return (
    <SPersonalPushupPlans>
      <h1> My personal pushup plans</h1>
      <SLink to='/pushups/trainingPlans/createPersonal'>Create new</SLink>
        
      <div>
        <h3>My existing personal pushup plans</h3>
        <p>none</p>
      </div>
    </SPersonalPushupPlans>
  );
};

export default PersonalPushupPlans;