import React from 'react';
import Trainings from './Trainings';
import styled from 'styled-components';
import { createPersonalPLANStarter, createPersonalTRAININGStarter, createPersonalSETStarter, } from './cratingObjectsfunctions'
import PlanContext from './ContextDefaults';
import Schedule from './schedule/Schedule';

// export const TestContext = React.createContext({a:'default Context in "createContext"'});
export const TestContext = React.createContext();

const SPlan = styled.div`
background-color: brown;
padding:3%;
`;


// const ShowPersonalPlanToUserToAdjust = ({ planToAdjust, plantToAdjust:{name, trainings} }) => {
const ShowPersonalPlanToUserToAdjust = ({ planToAdjust }) => {
  let { name, trainings, schedule } = planToAdjust;

  const planWMethods = {
    ...planToAdjust,
    // pushTraining(training) {this.trainings.push(training)}
    newTraining() {
      this.trainings.push(createPersonalTRAININGStarter());
      return this;
    }
    // newTraining () { console.log("this: ",planWMethods) }
    // newTraining () { this.trainings) }
  }


  //state
  const [plan, updatePlan] = React.useState(planWMethods);
  // console.log("plan in Personalstate now:", plan);
  const planPackage = { plan, updatePlan };


  // const PlanContext = React.createContext({});



  const handleTest = () => {
    // planPackage.plan.newTraining();
    // plan.newTraining();
    // console.log(plan);
    // console.log(planPackage);
    // console.log(plan.newTraining());

    // updatePlan(plan.newTraining());

    // planPackage.plan.newTraining();
    // updatePlan(planPackage);
    plan.newTraining();
    updatePlan({ ...plan });
    console.log(plan);
  }






  const handleFormSubmit = (e) => {
    e.preventDefault();
    // console.log("submit event:", e);
    let form = document.querySelector('#personalPlanForm');
    console.log("submit event:", form.TrainingName1.value);
  }

  const [state1,setState1] = React.useState({b:'default useContext',counter:0})
  // console.log("state1=context now:", state1);
  return (
    <SPlan>
      <PlanContext.Provider value={planPackage}>
        {/* <form id='personalPlanForm'> */}
          <h3>Plan name: <input type='text' placeholder='mandatory field' name='planName' defaultValue={name}></input></h3>
          <Trainings trainings={trainings} />
          <h4>Schedule/Rota</h4><br /><br /><br />
          <TestContext.Provider value={{state1,setState1}}>
            <Schedule>
              {/* <p>context1: {}</p> */}
            </Schedule>
          </TestContext.Provider>
          <button type="submit" onClick={handleFormSubmit}>Save Plan</button>
          <button onClick={handleTest}>Plan test</button>
        {/* </form> */}
      </PlanContext.Provider>
    </SPlan>
  );
};

export default ShowPersonalPlanToUserToAdjust;